/* Delivery Page Styles */
#delivery-hero {
    padding: 60px 0 60px 0;
    position: relative;
    background-color: #f8f9fa; /* A light grey for the checkered background */
    z-index: 10;
}

#delivery-hero .pixel-flower {
    max-width: 80px;
    margin-bottom: 20px;
}

#delivery-hero h1 {
    margin-bottom: 40px;
    font-family: "Proxima Nova Extra Condensed", sans-serif;
    font-weight: 800;
    font-size: 40px;
    line-height: 60px;
    color: #000;
}

#delivery-hero .hero-layer-pink-container {
    position: absolute;
    top: 75%;
    right: 2.5%;
    transform: translateY(-50%);
    width: 270px;
    z-index: 10;
}

#delivery-hero .hero-layer-pink {
    width: 100%;
    height: auto;
    z-index: 10;
}

#locations {
    padding: 120px 0 250px 0;
    position: relative;
    z-index: 1;
    background-color: #FBF5ED;
}

#locations .location-title {
    font-weight: 700;
    margin-bottom: 50px;
}

.location-card {
    border: 2px solid #000;
    margin-bottom: 30px;
}

.location-card .card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    color: #fff;
}

.location-card .card-header h3 {
    margin: 0;
    font-size: 1.3rem;
    font-weight: 700;
    font-family: "Proxima Nova Extra Condensed", sans-serif;
    color: #fff;
}

.location-card .card-header .arrow {
    font-size: 2rem;
    color: #fff;
    text-decoration: none;
}

.location-card img {
    width: 100%;
    height: auto;
}

.location-card .card-body {
    padding: 20px;
}

.location-card.nogal .card-header,
.location-card.nogal .card-body {
    background-color: #1abc9c;
}

.location-card.castellana .card-header,
.location-card.castellana .card-body {
    background-color: #e5407d;
}

.location-card .card-body p {
    color: #fff;
    margin-bottom: 1rem;
    font-size: 0.9rem;
    font-family: "Quicksand", sans-serif;
    font-weight: 500;
    font-style: italic;
}

#locations .delivery-background-pattern {
    position: absolute;
    bottom: 170px;
    left: 0;
    width: 100%;
    height: 145px;
    background-image: url('../img/dark-green-cross-layer-x.png');
    background-repeat: repeat;
    z-index: -1;
}
